import React from 'react';
import { Map } from 'immutable';

export default function makeGridLayoutPlaceholder(presenter) {
  const Placeholder = ({ config = new Map(), onSelectPresenterForEditing }) => {
    const rowIdx = config.get('rowIdx');
    const colIdx = config.get('colIdx');

    return (
      <div style={{width: '100%', height: '100%'}}>
        <button
          onClick={() => {
            onSelectPresenterForEditing(['config', rowIdx, colIdx]);
          }}>
          Set presenter
        </button>
      </div>
    );
  };

  return presenter({
    hidden: true
  })(Placeholder);
}
